import  wx from  "weixin-js-sdk";

var JSSDK = {


  /**
   * 
   *
   * @param type
   * @returns {Promise}
   */
  getLocation : function(type){
    return new Promise(function(resolve,reject){
      // wgs84gpsopenLocation'gcj02'
      let type = type || 'wgs84';
      wx.getLocation({
        type: type,
        success : function (res) {
          //res.latitude; // 90 ~ -90
          //res.longitude; // 180 ~ -180
          //res.speed; // /
          //res.accuracy; // 
          resolve(res);
        },
        cancel : function(res) {
          reject({state : 10002, desc : ''});
        },
        fail : function(){
          reject({state : 10001, desc : ''});
        }
      });

    });
  },

  openLocation : function(latitude,longitude){
    wx.openLocation({
      latitude: parseFloat(latitude), // 90 ~ -90
      longitude: parseFloat(longitude), // 180 ~ -180
      name: '', // 
      address: '', // 
      scale: 28, // ,,1~28
      infoUrl: '' // ,
    });

  },

  /**
   * 
   * @param option
   * @returns {Promise}
     */
  chooseImage : function(option){
    option = option || {
        count : 9,
        // 
        sizeType: ['original', 'compressed'],
        // 
        sourceType: ['album', 'camera']};

    return new Promise(function(resolve,reject){
        wx.chooseImage({
          count: option.count,
          sizeType: option.sizeType,
          sourceType: option.sourceType,
          success: (res)=>{
            resolve(res);
          },
          fail : function(){
            reject({state : 10001, desc : ''});
          }
        });
    });
  },


  /**
   * 
   * @param option
   * @returns {Promise}
     */
  uploadImage : function(localId,option){

    option = option || {
        // 1
        isShowProgressTips : 1};

    return new Promise(function(resolve,reject){

        wx.uploadImage({
          localId : localId,
          isShowProgressTips: option.isShowProgressTips,
          success : function(res){
            resolve(res);
          },
          fail : function(){
            reject({state : 10001, desc : ''});
          }
        });

    });
  },


  toggleMenu : function(isShow,type){
    let spreadMenu;
    if(isShow){
      //
      if(type == "spread"){
        spreadMenu = [
          'menuItem:share:appMessage',
          'menuItem:share:timeline',
          'menuItem:share:qq',
          'menuItem:share:weiboApp',
          'menuItem:favorite',
          'menuItem:share:facebook',
          'menuItem:share:QZone'
        ];
      }
      wx.showMenuItems({menuList : spreadMenu});
    }
    else{
      wx.hideAllNonBaseMenuItem();
    }


  },

  recursionImage : function(localIds,option,callBack){
    option = option || {
        // 1
        isShowProgressTips : 1};
    var localId = localIds.pop();
    wx.uploadImage({
      localId : localId,
      isShowProgressTips: option.isShowProgressTips,
      success : function(res){
        let state = 1;

        if(localIds.length > 0){
          JSSDK.recursionImage(localIds,option,callBack);
          state = 0;
        }
        else{
          state = 1;
        }
        callBack(state,{localId : localId, serverId : res.serverId});
      }
    });
  },

  /**
   * 
   * @param option
   */
  chooseAndUploadImage : function(option){
    return new Promise(function(resolve,reject){
      JSSDK.chooseImage(option)
        .then(function(res){
          if(res.localIds.length  <= 0){
            resolve([]);
            return;
          }

          let result = [];
          JSSDK.recursionImage(res.localIds,option,function(state,obj){
            result.push(obj);

            if(state == 1){
              resolve(result);
            }
          });

        },function(err){
          reject(err);
        });
    });


  },
  previewImage : function (urls,index) {
    index = index || 0;
    wx.previewImage({
      current: urls[index], // http
      urls: urls // http
    });

  },

  wxPay : function(obj){
    return new Promise(function(resolve,reject){
      wx.chooseWXPay({
        timestamp: obj.timestamp, // jssdktimestamptimeStampS
        nonceStr: obj.nonceStr, //  32 
        package: obj.package, // prepay_idprepay_id=***
        signType: obj.signType, // 'SHA1''MD5'
        paySign: obj.paySign, // 
        success: function (res) {
          // 
          resolve(res);
        },
        fail : function(res) {
          reject({state : 500, desc : res});
        },
        cancel:function(res){
          //
          reject({state : 10000, desc : res});
        }

      });

    });
  },

  sharePage  : function(shareData){
      wx.onMenuShareAppMessage(shareData);
      wx.onMenuShareTimeline(shareData);
      wx.onMenuShareQQ(shareData);
      wx.onMenuShareWeibo(shareData);
  },

  isWX :function(){
    var ua = navigator.userAgent.toLowerCase();//
    if (ua.match(/MicroMessenger/i) == "micromessenger") {
      return true;
    }
    return false;
  }
};

module.exports = JSSDK;



